input[type=password],
input[type=email] {
    width: 100%;
    padding: 15px 0 15px 8px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
}

input[type=password]:focus,
input[type=email]:focus {
    background-color: #ddd;
    outline: none;
}

input[type=submit] {
    background-color: #04AA6D;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    padding: 12px 20px;
}

input[type=submit]:hover {
    background-color: #450a49;
}


.model1 {
    /* position: fixed; */
    /* z-index: 1; */
    width: 100%;
    height: 100%;
    /* background-color: #474e5d; */
    background-color: #f8f9fa;
    /* top: 0; */
    left: 0;
    padding-top: 70px;
    overflow: auto;
}

.model-content1 {
    background-color: #fefefe;
    margin: 5% auto 23% auto;
    border: 1px solid #888;
    width: 30%;
}

.container1 {
    padding: 16px;
}

hr {
    margin-bottom: 25px;
    border: 1px solid #f1f1f1;
}

/*For Laptop View*/
@media screen and (max-width:768px) {
    .model-content1 {
        width: 50%;
    }
}

/*For Mobile View*/
@media screen and (max-width:480px) {
    .model-content1 {
        width: 80%;
    }
}

@media screen and (max-width:320px) {
    .model-content1 {
        width: 95%;
    }
}