.model-content3 {
    margin: 5% auto 15% auto;
}

.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 500px;
    margin: auto;
    text-align: center;
    font-family: arial;
}

.title {
    color: grey;
    font-size: 18px;
}

img {
    /* margin: auto; */
    width: 100%;
    height: 260px;
}

/*For Laptop View*/
@media screen and (max-width:768px) {
    .card {
        max-width: 450px;
    }
}

/*For Mobile View*/
@media screen and (max-width:480px) {
    .card {
        max-width: 550px;
    }

    .model-content3 {
        margin: 3% 3% 15% 3%;
    }
}

@media screen and (max-width:320px) {
    .card {
        max-width: 650px;
    }
}